.player-card {
  width: 50%;
  height: 100vh;
  /* Background pattern from Toptal Subtle Patterns */
  background-image: url(data:image/png;base64,);
  background-blend-mode: multiply;
  float: left;
  box-sizing: border-box;
  text-align: center;
  position: relative;
}

.player-card__active {
  position: absolute;
  top: 50%;
  width: 7%;
  height: 20%;
  background-color: rgba(0, 0, 0, 0.788);
  -webkit-transform: translate(0%, -50%);
          transform: translate(0%, -50%);
  z-index: 2000;
}
.player-card__active--1 {
  right: 0;
  -webkit-clip-path: polygon(100% 0, 0 50%, 100% 100%);
          clip-path: polygon(100% 0, 0 50%, 100% 100%);
}
.player-card__active--2 {
  left: 0;
  -webkit-clip-path: polygon(0 0, 100% 50%, 0 100%);
          clip-path: polygon(0 0, 100% 50%, 0 100%);
}

.player-card h3 {
  letter-spacing: 3px;
  font-weight: 200;
  text-transform: uppercase;
}

.player-card__score {
  font-weight: 100;
  padding: 5px;
  margin: 0 auto;
}
.player-card__content {
  width: 90%;
}
.player-card__buttons {
  position: absolute;
  bottom: 2%;
  right: 40%;
}
.clearfix::after {
  content: "";
  height: 0;
  display: table;
  clear: both;
}

.btn {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.788);
  color: white;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 3px;
}
.btn-play {
  padding: 5px 5px 0px 5px;
  font-size: 20px;
  margin: 5px;
}
.btn-set {
  padding: 5px 5px;
  display: block;
  font-size: 14px;
  margin: 10px auto 10px auto;
}
.btn-set:last-child {
  pointer-events: auto;
}

.btn:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: rgba(119, 40, 72, 0.322);
}
.btn:active {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  background-color: rgba(119, 40, 72, 0.322);
}

.btn-sel {
  width: 70%;
  font-size: 14px;
  display: block;
  background-color: transparent;
  border: 1px solid black;
  outline: none;
  margin-top: 10px;
  margin: 6% auto 3% auto;
}

.btn-check {
  margin-top: 60px;
  color: black;
}
.btn-check__label {
  font-family: "Lato", sans-serif;
  font-size: 13px;
}

